:root {
  --white: #ffffff;
  --black: #000000;
  --blue: #215398;
  --gray: #f1f6fc;
  --light-color: #808080;
  --dark-color: #2d3236;
  font-size: 16px;
  --font-family: "Rubik", Arial, sans-serif;
  --font-weight: 300;
  --line-height: 1.6;
  --line-height--headings: 1.2;
}

body {
  background-color: var(--white);
  color: var(--dark-color);
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  border: 0;
}

body {
  font-size: 1rem;
  font-family: var(--font-family);
  font-weight: var(--font-weight);
  line-height: var(--line-height);
}

h1,
h2,
h3,
h4,
h5,
h6 {
  line-height: var(--line-height--headings);
  font-weight: var(--font-weight);
  margin: 0;
  padding: 0;
}

p {
  margin: 0;
  padding: 0;
}

.font-30 {
  font-size: 1.875rem;
}

.text-center {
  text-align: center;
}

.text-dark {
  color: var(--dark-color);
}

.text-light {
  color: var(--light-color);
}

.text-white {
  color: var(--white);
}

.container {
  padding-left: 0.75rem;
  padding-right: 0.75rem;
  margin: 0 auto;
  --container-width: calc(100% - 1.5rem);
  width: var(--container-width);
}
@media screen and (min-width: 576px) {
  .container {
    --container-width: calc(
      540px - 1.5rem
    );
  }
}
@media screen and (min-width: 768px) {
  .container {
    --container-width: calc(
      767px - 1.5rem
    );
  }
}
@media screen and (min-width: 992px) {
  .container {
    --container-width: calc(
      991px - 1.5rem
    );
  }
}
@media screen and (min-width: 1200px) {
  .container {
    --container-width: calc(
      1180px - 1.5rem
    );
  }
}

.header {
  padding-top: 1.4375rem;
  padding-bottom: 1.4375rem;
}
.header__logo {
  display: block;
  width: 100%;
  max-width: 10rem;
  height: auto;
}

.section {
  padding: 3.125rem 0;
  background-color: var(--gray);
  display: flex;
  flex-direction: column;
}
.section--blue {
  background-color: var(--blue);
}

.section-header {
  margin-bottom: 2.5rem;
  --section-header-color: var(--color-dark);
  color: var(--section-header-color);
  display: flex;
  flex-direction: column;
  text-align: center;
  gap: 0.3125rem;
}
.section-header__title {
  font-size: 2.5rem;
}
.section-header__description {
  font-size: 1.25rem;
}
.section-header--blue {
  --section-header-color: var(--white);
}

.slider {
  position: relative;
  padding: 0 4.375rem;
}
.slider--marginOnTop {
  margin-top: 1.5rem;
}
.slider__list {
  list-style: none;
  display: flex;
  align-items: stretch;
}
.slider__item {
  box-sizing: border-box;
  --drop-shadow-size: 0.25rem;
  margin-top: var(--drop-shadow-size);
  margin-bottom: var(--drop-shadow-size);
  filter: drop-shadow(0 var(--drop-shadow-size) var(--drop-shadow-size) rgba(0, 0, 0, 0.02));
}
.slider__article {
  width: calc(100% - 20px);
  height: 100%;
  background-color: var(--white);
}
.slider__inner {
  padding: 2.25rem 1.75rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 0.75rem;
  text-align: center;
}
.slider__title {
  font-size: 1.25rem;
}
.slider__content {
  color: var(--light-color);
}
.slider__image {
  max-width: 100%;
  height: auto;
}
.slider__control {
  position: absolute;
  inset: 0;
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
}
.slider__button {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  appearance: none;
  padding: 0;
  width: 3rem;
  height: 3rem;
  border: 0;
  border-radius: 50%;
  background-image: url("../../img/arrow.svg");
  background-position: center;
  background-repeat: no-repeat;
  background-color: transparent;
  cursor: pointer;
}
.slider__button:hover {
  background-color: rgba(255, 255, 255, 0.05);
}
.slider__button--prev {
  left: 0;
}
.slider__button--next {
  right: 0;
  transform: translateY(-50%) rotate(180deg);
}